.backdrop {
  z-index: 11;
  visibility: hidden;
  pointer-events: none;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 250ms var(--distribution-time), visibility 250ms var(--distribution-time);
  background: rgba(0, 0, 0, .65);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.active {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__table.active, .modal__room.active, .modal__price.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translate(-50%, -50%)scale(.9);
}

