.details-hotel {
  background-color: black;
  padding-top: 24px;
  padding-bottom: 40px;
  max-width: 414px;

  margin: 0 auto;
  background-image: linear-gradient(var(--hero-bcg), var(--hero-bcg)),
    url(../images/section/hotel/main-hotel-bg-mobile@1x.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(var(--hero-bcg), var(--hero-bcg)),
      url('../images/section/hotel/main-hotel-bg-mobile@2x.jpg');
  }
  @media screen and (min-width: $tablet) {
    padding-bottom: 15px;
    & {
      max-width: 1024px;
      background-image: linear-gradient(var(--hero-bcg), var(--hero-bcg)),
        url(../images/section/hotel/main-hotel-bg-tablet@1x.jpg);
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      & {
        background-image: linear-gradient(var(--hero-bcg), var(--hero-bcg)),
          url(../images/section/hotel/main-hotel-bg-tablet@2x.jpg);
      }
    }
  }
  @media screen and (min-width: $desktop) {
    padding-bottom: 65px;
    & {
      max-width: 1280px;
      background-image: linear-gradient(var(--hero-bcg), var(--hero-bcg)),
        url(../images/section/hotel/main-hotel-bg-desktop@1x.jpg);
    }
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      & {
        background-image: linear-gradient(var(--hero-bcg), var(--hero-bcg)),
          url(../images/section/hotel/main-hotel-bg-desktop@2x.jpg);
      }
    }
  }

  &__container {
    padding-right: 40px;
    @media screen and (min-width: $tablet) {
      display: flex;
      gap: 32px;
      padding-right: 47px;
    }
    @media screen and (min-width: $desktop) {
      padding-right: 85px;
    }
  }

  &__main {
    min-width: 0;
    @media screen and (min-width: $tablet) {
      // display: flex;
      // flex-direction: column;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    text-align: right;
    max-width: 267px;
    gap: 12px;

    @media screen and (min-width: $tablet) {
      max-width: 662px;
      align-items: flex-end;
    }

    @media screen and (min-width: $desktop) {
      max-width: 890px;
      align-items: flex-end;
    }
  }

  &__logo {
    @include logo($size: 40px, $color: var(--white-color));
    text-transform: uppercase;

    @media screen and (min-width: $tablet) {
      margin-bottom: 16px;
      font-size: 64px;
      text-align: end;
      font-size: 64px;

      width: 202px;
    }
    @media screen and (min-width: $desktop) {
      margin-right: 0;
    }
  }

  &__message {
    padding-left: 17px;
    max-width: 258px;

    @media screen and (min-width: $tablet) {
      max-width: 549px;
      padding-left: 0;
    }

    @media screen and (min-width: $desktop) {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }

  &__title {
    margin-bottom: 20px;
    text-align: right;

    font-size: 20px;
    line-height: 1.2;
    color: var(--white-color);

    @media screen and (min-width: $tablet) {
      margin-bottom: 8px;
      font-size: 24px;
      line-height: 1.21;
    }
  }

  &__text {
    color: var(--section-text-color);
    text-align: left;
    margin-bottom: 48px;
    font-size: 12px;
    letter-spacing: 0.02em;
    line-height: 1.25;

    font-weight: 600;

    @media screen and (min-width: $tablet) {
      max-width: 549px;
      font-size: 16px;
    }

    @media screen and (min-width: $desktop) {
      max-width: 565px;
      margin-bottom: 40px;
    }
  }

  &__btn-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;

    @media screen and (min-width: $tablet) {
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      flex-direction: column;
      margin-bottom: 80px;
    }
  }

  &__btn {
    width: 236px;
    max-height: 48px;

    @media screen and (min-width: $desktop) {
      width: 238px;
    }
  }

  &__btn-price {
    padding: 14px 24px;
    width: 236px;
    max-height: 48px;

    @media screen and (min-width: $desktop) {
      width: 238px;
    }
  }
}

.hidden-mobile {
  @media screen and (max-width: 1023px) {
    position: absolute;
    white-space: nowrap;
    width: 1px;
    height: 1px;
    overflow: hidden;
    border: 0;
    padding: 0;
    clip: rect(0, 0, 0, 0);
    clip-path: inset(50%);
    margin: -1px;
  }
}
